.App {
  height: 100vh;
  width: 100vw;
  border: 0;
  margin: 0;
}

canvas {
  cursor: pointer;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1px;
  font-weight: 300;
}

.logocontainer {
  position: absolute;

  padding: 30px;
  top: 0;
  left: 2%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  .logo {
    height: 110px;
  }

  .logoText {
    height: 85px;
    margin-left: 15px;
  }

  @media only screen and (max-width: 600px) {
    .logo {
      height: 60px;
    }
    .logoText {
      height: 45px;
    }
  }
}

section {
  position: absolute;
  color: white;
  top: 40%;
  left: 5%;
  z-index: 1;
}

p span {
  font-size: 35px;
}

.container p {
  position: relative;
}

.switch {
  display: inline-block;
  height: 34px;
  position: relative;
  width: 60px;
}

#countryName {
  position: absolute;
  bottom: calc(50% + 220px);
  left: calc(50% + 220px);
  color: white;
  text-transform: uppercase;
}
